@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&family=Raleway:wght@300&family=Saira+Condensed:wght@300&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  text-decoration: none;
  outline:none;
  color: white;
}
.btn{
    position: relative;
}

.btn::after{
    content: '';
    position: absolute;
    width: 83%;
    height: 0.175rem;
    left: 8px;
    bottom: -5px;
    background: #00D0FF;
}

.btn-left::after{
    transform: scale(0,1);
    transform-origin: 0% 100% ;
    transition: transform 0.5s ease;
}

.btn-left:hover::after{
    transform: scale(1,1);
}

.none{
    text-decoration: none;
    list-style: none;
}

.line{
    width: 33px;
    background-color: rgb(255, 255, 255);
    height: 4px;
    margin: 5px 3px;
}

.hamburger{
    display: none;
}

.cross{
    display: none;
}

.point:hover{
    color:#00D0FF;
}

@media only screen and (max-width: 800px) {
    .cross{
        display: inline-block;
    }
    .navbar-resp{
        flex-direction: column;
    }
    .hidden{
        display: none;
    }
    .visible{
        display: block;
    }
    .hamburger{
        display: block;
    }
    .hide{
        display: none;
    }
    .line{
        display: block;
    }
}
#about{
    height: 50vh;
}
.form-input-bg{
    background-color: #ffffff54;
    border-radius: 8px;
}
@media only screen and (max-width:450px){
    .width{
        width: 100%;
        margin: 2%;
    }
    .about-padding{
        padding: 0;
    }
}
@media only screen and (max-width:700px){
    .home-background{
        height:100%;
    }
    .margin-6{
        margin-right:0;
        margin-left: 0;
    }
}
@media only screen and (max-width: 500px) {
    .wrapping{
        flex-direction: column;
    }
    .width{
        width: 100%;
    }
    .padding{
        padding: 0;
    }
    .margin-horizontal{
        margin-left: 0;
        margin-right: 0;
        padding: 0;
    }
}

